"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Calculator, Utensils, FileText } from "lucide-react"

interface BMIResult {
  bmi: string;
  status: string;
  idealWeight: string;
  weightDiff: string;
}

interface NutritionResult {
  bmr: number;
  totalCalories: number;
}

export default function Home() {
  const [formData, setFormData] = useState({
    name: "",
    weight: "",
    height: "",
    age: "",
    gender: "female",
    activityLevel: "sedentary"
  })

  const [bmiResult, setBmiResult] = useState<BMIResult | null>(null)
  const [nutritionResult, setNutritionResult] = useState<NutritionResult | null>(null)

  const calculateBMI = () => {
    if (!formData.weight || !formData.height) return;
    
    const weight = parseFloat(formData.weight)
    const height = parseFloat(formData.height) / 100 // Convert to meters
    
    if (weight <= 0 || height <= 0) return;
    
    const bmi = weight / (height * height)
    const idealWeight = 22 * (height * height) // Using BMI 22 as ideal
    const weightDiff = Math.abs(weight - idealWeight)
    
    let status = ""
    if (bmi < 18.5) status = "Kekurangan Berat"
    else if (bmi < 25) status = "Normal"
    else if (bmi < 30) status = "Kelebihan Berat"
    else status = "Obesitas"

    setBmiResult({
      bmi: bmi.toFixed(1),
      status,
      idealWeight: idealWeight.toFixed(1),
      weightDiff: weightDiff.toFixed(1)
    })
  }

  const calculateNutrition = () => {
    const w = parseFloat(formData.weight)
    const h = parseFloat(formData.height)
    const a = parseFloat(formData.age)
    
    let bmr = 0
    if (formData.gender === 'male') {
      bmr = 66.5 + (13.75 * w) + (5.003 * h) - (6.75 * a)
    } else {
      bmr = 655.1 + (9.563 * w) + (1.850 * h) - (4.676 * a)
    }
    
    const multipliers = {
      sedentary: 1.2,
      light: 1.375,
      moderate: 1.55,
      very: 1.725
    }
    
    const totalCalories = bmr * multipliers[formData.activityLevel as keyof typeof multipliers]
    
    setNutritionResult({
      bmr: Math.round(bmr),
      totalCalories: Math.round(totalCalories)
    })
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-teal-600 to-teal-700">
      <div className="container mx-auto px-4 py-16 text-center text-white">
        <h1 className="text-4xl font-bold mb-4">Selamat Datang di GiziLa</h1>
        <p className="text-xl mb-8">Sistem Informasi Gizi Harian dan Indeks Massa Tubuh</p>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 max-w-4xl mx-auto mb-12">
          <Card className="p-6 text-center bg-white/10 backdrop-blur-lg text-white border-0">
            <Calculator className="w-12 h-12 mx-auto mb-4" />
            <h3 className="font-semibold mb-2">Hitung IMT</h3>
            <p className="text-sm opacity-80">Mengukur indeks massa tubuh dan berat ideal Anda.</p>
          </Card>
          <Card className="p-6 text-center bg-white/10 backdrop-blur-lg text-white border-0">
            <Utensils className="w-12 h-12 mx-auto mb-4" />
            <h3 className="font-semibold mb-2">Kalkulasi Kalori</h3>
            <p className="text-sm opacity-80">Menentukan kebutuhan kalori harian Anda.</p>
          </Card>
          <Card className="p-6 text-center bg-white/10 backdrop-blur-lg text-white border-0">
            <FileText className="w-12 h-12 mx-auto mb-4" />
            <h3 className="font-semibold mb-2">Saran Gizi</h3>
            <p className="text-sm opacity-80">Memberi panduan umum pola makan yang seimbang.</p>
          </Card>
        </div>
      </div>

      <div className="container mx-auto px-4 py-8">
        <div className="bg-white rounded-lg p-8 shadow-lg">
          <h2 className="text-2xl font-bold mb-6 text-center">Kenali Kebutuhan Gizi Anda</h2>
          <p className="text-center mb-8 text-gray-600">
            Dengan GiziLa, Anda bisa menghitung indeks massa tubuh dan kebutuhan kalori harian berdasarkan aktivitas Anda.
          </p>

          <Card className="max-w-2xl mx-auto p-6">
            <div className="grid grid-cols-2 gap-4">
              <div className="col-span-2">
                <label className="block text-sm font-medium mb-1">Nama</label>
                <Input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  placeholder="Masukkan nama Anda"
                  className="w-full"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Berat Badan (kg)</label>
                <Input
                  type="number"
                  value={formData.weight}
                  onChange={(e) => setFormData({...formData, weight: e.target.value})}
                  placeholder="60"
                  className="w-full"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Tinggi Badan (cm)</label>
                <Input
                  type="number"
                  value={formData.height}
                  onChange={(e) => setFormData({...formData, height: e.target.value})}
                  placeholder="165"
                  className="w-full"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Usia</label>
                <Input
                  type="number"
                  value={formData.age}
                  onChange={(e) => setFormData({...formData, age: e.target.value})}
                  placeholder="25"
                  className="w-full"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Jenis Kelamin</label>
                <select
                  className="w-full p-2 border rounded"
                  value={formData.gender}
                  onChange={(e) => setFormData({...formData, gender: e.target.value})}
                >
                  <option value="male">Laki-laki</option>
                  <option value="female">Perempuan</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Tingkat Aktivitas</label>
                <select
                  className="w-full p-2 border rounded"
                  value={formData.activityLevel}
                  onChange={(e) => setFormData({...formData, activityLevel: e.target.value})}
                >
                  <option value="sedentary">Tidak Aktif</option>
                  <option value="light">Sedikit Aktif</option>
                  <option value="moderate">Cukup Aktif</option>
                  <option value="very">Sangat Aktif</option>
                </select>
              </div>
              <div className="col-span-2">
                <Button 
                  className="w-full bg-teal-600 hover:bg-teal-700 text-white"
                  onClick={calculateBMI}
                >
                  Hitung
                </Button>
              </div>
            </div>
          </Card>

          {bmiResult && (
            <Card className="max-w-2xl mx-auto mt-8 p-6">
              <h3 className="text-xl font-bold mb-4">Hasil Indeks Massa Tubuh</h3>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <p className="text-sm text-gray-600">BMI:</p>
                  <p className="text-lg font-bold">{bmiResult.bmi}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Status:</p>
                  <p className="text-lg font-bold">{bmiResult.status}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Berat Ideal:</p>
                  <p className="text-lg font-bold">{bmiResult.idealWeight} kg</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Selisih Berat:</p>
                  <p className="text-lg font-bold">{bmiResult.weightDiff} kg</p>
                </div>
              </div>
              <Button
                className="w-full mt-6 bg-teal-600 hover:bg-teal-700 text-white"
                onClick={calculateNutrition}
              >
                Hitung Gizi Harian Anda
              </Button>
            </Card>
          )}

          {nutritionResult && (
            <Card className="max-w-2xl mx-auto mt-8 p-6">
              <h3 className="text-xl font-bold mb-4">Kebutuhan Gizi Harian</h3>
              <div className="space-y-4">
                <div>
                  <p className="text-sm text-gray-600">BMR (Basal Metabolic Rate):</p>
                  <p className="text-lg font-bold">{nutritionResult.bmr} kalori</p>
                  <p className="text-xs text-gray-500">Jumlah kalori yang dibutuhkan tubuh saat istirahat total</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Total Kalori Harian:</p>
                  <p className="text-lg font-bold">{nutritionResult.totalCalories} kalori</p>
                  <p className="text-xs text-gray-500">Perhitungan menggunakan rumus Harris Benedict berdasarkan aktivitas Anda</p>
                </div>
                <div className="mt-6 p-4 bg-gray-50 rounded-lg">
                  <h4 className="font-bold mb-2">Panduan Umum Pola Makan Seimbang:</h4>
                  <ul className="list-disc pl-5 space-y-2 text-sm text-gray-600">
                    <li>Konsumsi makanan beragam dari semua kelompok makanan</li>
                    <li>Perbanyak konsumsi sayuran dan buah-buahan</li>
                    <li>Pilih protein sehat seperti ikan, ayam, telur, dan kacang-kacangan</li>
                    <li>Batasi konsumsi gula, garam, dan lemak</li>
                    <li>Minum air putih yang cukup (minimal 8 gelas per hari)</li>
                    <li>Lakukan aktivitas fisik secara teratur</li>
                  </ul>
                </div>
              </div>
            </Card>
          )}
        </div>
      </div>
    </main>
  )
}
